.aboutBanner{
   z-index: 3;
   max-width: 600px;
   position: absolute;
   padding: 8px 24px;
   display:flex ;
   flex-direction: column;
   align-items: center;
   color: #01bf71;
   cursor: pointer;
}

.aboutH1{
   font-size: 40px;
   padding: 2px;
   margin: 5px;


}
.aboutP
{
    margin-top: 10px;
    font-size: 35px;
    text-align: center;
}
@media screen and (max-width: 960px)
{
    .aboutH1{
        font-size: 30px !important;
        padding: 0px;
        margin: 5px;
        text-decoration: underline dashed black;
     
     }
     .aboutP
{
    margin-top: 9px;
    font-size: 30px;
    text-align: center;
}
}

@media screen and (max-width: 420px)
{
    .aboutH1{
        font-size: 25px ;
        padding: 0px;
        margin: 5px;
        text-decoration: underline dashed black;
     
     }

     .aboutP
{
    margin-top: 5px;
    font-size: 25px;
    text-align: center;
}
}